<template>
    <div class="item-manage">
        <adt-search-folder :searchRouter="{query: {name: searchData.name}}" class="query-tit">
            <template slot="table-btn-group">
                <Form ref="templateForm" :model="searchData" :label-width="90">
                    <FormItem label="数据项名称：" prop="name">
                        <Input type="text" v-model="searchData.name" clearable></Input>
                    </FormItem>
                </Form>
            </template>
        </adt-search-folder>
        <div class="content">
            <div class="btns-paginator">
                <adt-button>
                    <Button @click="addItemData">新增数据项<Icon type="ios-add-circle-outline" /></Button>
                </adt-button>
                <adt-paginator @getPages="getPages" :currentPage="~~page" :total="~~total" path="/standard-mapper/datasource-manage" />
            </div>
            <template class="table-data">
                <Table :columns="columns" :data="templateList" stripe>
                    <template slot-scope="{ row }" slot="action">
                        <Icon type="md-create" @click="modifyShow(row)" />
                        <Icon type="ios-close" @click="deleteShow(row)"/>
                    </template>
                </Table>
            </template>
        </div>
        <adt-reconfirm-modal :showModal="isActionDel" @on-visible-change="visibleChange" @on-ok="allDelClick()" />
    </div>
</template>

<script>
import { apiTemplateDataQuery, getTemplateDataQuery, apiTemplateDataDel } from '@/api/template'
import { apiDictionaryQueryByCacheTypeKey } from '@/api/dictionary'
import paginator from '@/mixins/paginator'
import validateRules from '@/libs/validateRules'
export default {
    mixins: [paginator],
    name: 'ItemManage',
    data() {
        return {
            searchData: {
                name: ''
            },
            modalTitle: '',
            columns: [
                {title: '数据类型',key: 'name',align: 'center'},
                {title: '数据显示名',key: 'label',align: 'center'},
                {title: '数据库字段',key: 'il8nLangCode',align: 'center'},
                {title: '操作', slot: 'action' ,align: 'center'},
            ],
            templateList: [],
            isModalShow: false,
            isActionDel: false,
            formItem: {
                nameAndLang: []
            },
            controlType: [],
            dataTypeArr: [],
            // 新增编辑区分  1为新增 2为编辑
            distinguish: 0,
            languageType: [],
            languageArray: [],
            selectLanguage: [],
            passData: {}
        }
    },
    mounted () {
    },
    methods: {
        test(val){
            const arr = val || []
            let array = []
            arr.forEach(item => {
                array.push({label: item.label, il8nLangCode: item.value})
            })
            this.selectLanguage = array
        },
        getPages (page) {
            this.page = page
        },
        visibleChange (changeStatus) {
            this.isActionDel = changeStatus
        },
        // 点击搜索调用查询接口
        dataInit () {
            this.getData()
        },
        getData () {
            const params = {
                page: this.page,
                size: 15,
                direction: 'asc',
                properties: '',
                name: this.searchData.name,
                // label: '数据项前端展示名称'
            }
            apiTemplateDataQuery({ data: params }).then(res => {
                this.templateList = res.data.content || []
                this.total = res.data.totalElements
            })
        },
        // 新增数据项
        addItemData () {
            this.isModalShow = true
            this.modalTitle = '新增数据项'
            this.distinguish = 1
        },
        // 修改数据项
        modifyShow (row) {
            this.distinguish = 2
            const params = {name: row.name}
            getTemplateDataQuery(params).then(res => {
                this.formItem = res.data
                this.languageArray = res.data.labelAndLanguage
                // 编辑时语言栏展示数据处理
                let arr = []
                res.data.labelAndLanguage.forEach(item => {
                    arr.push(item.il8nLangCode)
                })
                this.formItem.nameAndLang = arr
                // 当后台返回的校验规则为空时赋值[]
                this.formItem.rules = res.data.rules || []
                this.isModalShow = true
                this.modalTitle = '编辑数据项'
            })
        },
        deleteShow (row) {
            this.isActionDel = true
            this.passData = {name: row.name}
        },
        allDelClick () {
            console.log(this.passData)
            apiTemplateDataDel(this.passData).then(res => {
                if (res.code == 200) {
                    this.$Message.info("删除成功")
                    // 隐藏模态框
                    this.isActionDel = false
                    // 刷新列表
                    this.getData()
                } else {
                    this.$Message.info("删除失败")
                }
            })
        }
    },
}
</script>

<style lang="scss" scoped>
/deep/.ivu-form-item > .ivu-form-item-label{
    text-align: left;
}
// 数据项弹窗分栏
/deep/textarea.ivu-input{
    width: 560px;
}
.form-model{
    padding: 0 35px;
}
.input-width{
    width: 215px;
}
.item-manage{
    margin: 10px;
    .query-tit{
        .ivu-form-item{
            margin-bottom: 0;
            /deep/.ivu-form-item-label{
                text-align: left;
                padding: 10px 0;
            }
            .ivu-input-wrapper{
                width: 18%;
            }
        }
    }
    .content{
        margin-top: 20px;
        .ivu-icon{
            vertical-align: middle;
            cursor: pointer;
        }
        .ivu-table-wrapper{
            margin: 10px 0;
            box-shadow: 0px 1px 2px #9f9f9f;
            .ivu-table{
                overflow-y: scroll;
            }
        }
        .ivu-icon-ios-options-outline{
            font-size: 20px;
            transform: rotate(-90deg);
            margin-right: 8px;
        }
        .ivu-icon-ios-brush-outline{
            font-size: 20px;
        }
        .ivu-icon-ios-close{
            font-size: 34px;
        }
    }
    /deep/.ivu-modal{
        width: 76%!important;

    }
    .ivu-select-single .ivu-select-selection .ivu-select-selected-value{
        text-align: left!important;
        padding-left: 0!important;
    }
    .ivu-input{
        font-size: 12px;
    }
}
.modal-title{
    margin-bottom: 10px;
    .template-name{
        margin-right: 20px;
    }
}
.template-modal{
    width: 70%;
    margin: 0 auto;
    .ivu-form-item{
        margin-bottom: 10px;
    }
    .ivu-input-wrapper{
        width: 90%;
    }
    .template-name{
        /deep/.ivu-form-item-label{
            width: 40px!important;
        }
        .ivu-input-wrapper{
            width: 68%;
        }
    }
}
</style>
